@import url(https://fonts.googleapis.com/css2?family=MuseoModerno:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Barlow:800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/profile/static/media/diagonals.6a94fd4c.jpeg);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.title, .lina {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #2a9d8f;
    font-size: large;
    font-family: 'Raleway', sans-serif;

}


.lina {
    margin-bottom: 2vh;
}

.nav-bar {
    display: flex;
    justify-content: space-between;
    color: #caf0f8;
    font-weight: bold;
    background-color: black;
    height: 60px;
    font-family: 'MuseoModerno', cursive;
    margin-bottom: 30px;
}

.nav-bar-item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    list-style: none;
    padding-right: 60px;
    text-decoration: none;
    color: #caf0f8;
}

.nav-bar-link {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    list-style: none;
    padding-left: 60px;
    text-decoration: none;
    color: #caf0f8;
}


.carousel {
    margin-left: 10vh;
    margin-right: 10vh;
}

.image {
    /* display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;

    height: 60vh;
    margin: auto; */

    margin: auto;
    width: 80%;
    margin-top: 1vh;

}





#main-container {
    margin-left: 300px;
    margin-right: 300px;
    margin-top: 50px;
    overflow-anchor: none;
}

.main-about {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #f4a261;
    text-align: justify;
    line-height: 1.8;
    flex-direction: column;
    font-size: large;
    font-weight: 300;
    padding-top: 3vh;
    padding-bottom: 5vh;
}

.cc-grad {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    color: #00f5d4;
    text-decoration: none;
}

a:hover {
    color: #ff0054;
  }
  
a:active {
    color: black;
}



  

#main-project {
    display:flex;
    flex-direction: row;
    justify-content: space-around;
    margin-left: 100px;
    margin-right: 100px;
    padding-bottom: 40px;
    flex-grow: 1;
}

.link {
    text-align: center;
    list-style: none;
    text-transform: uppercase;
    width: 100%;
}

.link-title {
    text-decoration: none;
    color: #e76f51;
}

.project-card {
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    font-size: medium;
    color: #e76f51;
    margin: 1.5%;
    flex: 1 1;
}

.project-descr {
    display:flex;
    flex-direction: column;
    text-transform: none;
    text-align: justify;
    line-height: 1.7;
}

.tech-stack-title {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    color: #00f5d4;
}

.tech-stack {
    flex-wrap: wrap;
    text-align: center;
    line-height: 1.7;
    color: #00f5d4;
    min-height: 30px;
}

.image1, .image2, .image3 {
    display:flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    margin-top: 40px;
    margin-bottom: 20px;
    /* max-width: 500px; */
}

#travel {
    content: url(/profile/static/media/travel.c788929c.jpeg);
    height: 100%;
    width: 100%;
    align-items: center;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

#weather {
    content: url(/profile/static/media/weather.3736e064.jpeg);
    height: 100%;
    width: 100%;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

#baldur {
    content: url(/profile/static/media/baldur.34c7ee80.jpeg);
    height: 100%;
    width: 100%;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
#travel:hover, #weather:hover, #baldur:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    height: 400px;
    width: 500px;
}
footer {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    color: white;
    font-size: small;
    height: 2.5rem;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    clear: both;
    margin-top: 50px;
}
/* Nav Bar page */
@media (min-width: 280px) and (max-width: 1024px) {
    /* Nav Bar */
    .nav-bar {
        display: flex;
        justify-content: space-between;
        padding: 0%;
        margin: 0%;
    }

    .nav-bar-link {
        padding-left: 1vh;
    }

    .nav-bar-item {
        padding-right: 1vh;
    }

    .cc-grad {
        margin: 2vh;
    }

    /* Home page */
    .carousel, .image {
        margin: auto;
        width: 90%;
        height: 100%;
        margin-top: 1vh;
    }

    .title, .lina {
        margin: 1vh;
        font-size: medium;
    }

    /* About page */
    #main-container {
        margin: 6vw;
        display:flex;
        flex-direction: column;
    }

    /* Projects page */
    #main-project {
        margin: 3vw;
        display:flex;
        flex-direction: column;
    }
}
